.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;

    .team-name-section{
        grid-column: span 3;
        justify-self: start;
        align-self: flex-end;

        .team-name{
            font-size: 3.7rem;
            margin-bottom: 20px;
        }
    }

    .win-loss-section {
        justify-self: center;
        padding: 40px;
        text-align:center;
    }

    .match-detail-section{
        grid-column: span 4;
    }

    .more-link{
        align-self: center;
        justify-self: center;
    }

    .second-grid{
        grid-column: span 4;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 10px;  
        grid-template-rows: 200px;
    }
}